.SA_main-body .SA_pullquotebox {
    position: relative;
    padding-left: 0!important;
    // float: right;
    border: none;
    padding-left: 1.4rem;
    width: calc((100% * 8/14) - #{$margins+"px"});
}

.SA_main-body .SA_pullquotebox--right {
    float: right;
    margin-right: calc((-100% * 2/14) - #{$margins+"px"});
    margin-left: 1rem;
}

.SA_main-body .SA_pullquotebox--left {
    float: left;
    margin-left: calc((-100% * 2/14) - #{$margins+"px"});
    margin-right: 1rem;
}

.SA_main-body .SA_pullquotebox--left .SA_pullquote{
    margin-left: 0;
    text-align: right;
}

.SA_pullquotebox .SA_pullquote--right, .SA_pullquotebox .SA_pullquote--left{
    float: none;
    width: 100%;
}

.SA_pullquotebox .SA_caption-left-flush{
    // margin-top: 12px;
    margin-left: 1rem;
}

.SA_main-body .SA_pullQuotehash {
    height: 80%;
    width: 20px;
    position: absolute;

    // background-image: url('../img/graphics/diagonal-45-gray.svg');
    // background-size: 1800px 1800px;
    // opacity: 0.3;
}

.SA_pullquotebox--right .SA_pullQuotehash {
    top: 0;
    left: 0;
    transform:translate(-8px, 0px);
}

.SA_pullquotebox--left .SA_pullQuotehash {
    top: 0;
    right: 0;
    transform:translate(2px, 0px);
}

.SA_main-body .SA_h-5 {
