  // CHECKBOX START

  .SA_dropdown {
  font-family: 'Inter', sans-serif; 
    position: relative;
    font-size: 14px;
    color: #333;
    margin-top: 20px;

    .SA_dropdown-list {
      max-height: 300px;
      padding: 12px;
      background: #fff;
      position: absolute;
      top: 30px;
      left: 2px;
      right: 2px;
      box-shadow: 0 1px 2px 1px rgba(0, 0, 0, .15);
      transform-origin: 50% 0;
      transform: scale(1, 0);
      transition: transform .15s ease-in-out .15s;
      max-height: 400px;
      overflow-y: scroll;
      z-index: 40;
    }
    
    .SA_dropdown-option {
      display: block;
      padding: 8px 12px;
      opacity: 0;
      transition: opacity .15s ease-in-out;
    }
    
    .SA_dropdown-label {
      display: block;
      height: 30px;
      background: #fff;
      border: 1px solid #ccc;
      padding: 6px 12px;
      line-height: 1;
      cursor: pointer;
      
      &:before {
        content: '▼';
        float: right;
      }
    }
    
    &.on {
     .SA_dropdown-list {
        transform: scale(1, 1);
        transition-delay: 0s;
        
        .SA_dropdown-option {
          opacity: 1;
          transition-delay: .2s;
        }
      }
      
      .SA_dropdown-label:before {
        content: '▲';
      }
    }
    
    [type="checkbox"] {
      position: relative;
      top: -1px;
      margin-right: 4px;
    }
  }

  @media (max-width: 700px) {
    #SA_dropdown-list {
      max-height: 300px;
      }
  }

